'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import {
  Calendar,
  Clock,
  MessageSquare,
  Users,
  Image,
  FileText,
  UsersRound,
  CheckCircle,
  AlertCircle,
} from 'lucide-react';

type Stats = {
  reservations: {
    total: number;
    pending: number;
    confirmed: number;
    cancelled: number;
    completed: number;
  };
  messages: {
    total: number;
    unread: number;
  };
  gallery: {
    albums: number;
    images: number;
  };
  team: number;
  groups: number;
  banners: number;
};

type RecentReservation = {
  id: number;
  confirmation_code: string;
  nombre: string;
  apellidos: string;
  tipo_misa: string;
  reservation_date: string;
  status: string;
};

type RecentMessage = {
  id: number;
  nombre: string;
  email: string;
  asunto: string;
  created_at: string;
  is_read: boolean;
};

const STATUS_COLORS: Record<string, string> = {
  pending: 'bg-yellow-100 text-yellow-800',
  confirmed: 'bg-green-100 text-green-800',
  cancelled: 'bg-red-100 text-red-800',
  completed: 'bg-blue-100 text-blue-800',
  payment_pending: 'bg-orange-100 text-orange-800',
};

const STATUS_LABELS: Record<string, string> = {
  pending: 'Pendiente',
  confirmed: 'Confirmada',
  cancelled: 'Cancelada',
  completed: 'Completada',
  payment_pending: 'Pago Pendiente',
};

export default function AdminDashboard() {
  const [stats, setStats] = useState<Stats | null>(null);
  const [recentReservations, setRecentReservations] = useState<RecentReservation[]>([]);
  const [recentMessages, setRecentMessages] = useState<RecentMessage[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      const statsResponse = await fetch('/api/admin/stats');
      const statsData = await statsResponse.json();

      const reservationsResponse = await fetch('/api/admin/reservations?limit=5');
      const reservationsData = await reservationsResponse.json();

      const messagesResponse = await fetch('/api/admin/messages?limit=5');
      const messagesData = await messagesResponse.json();

      if (statsData.success) {
        setStats(statsData.data);
      }

      if (reservationsData.success) {
        setRecentReservations(reservationsData.data || []);
      }

      if (messagesData.success) {
        setRecentMessages(messagesData.data || []);
      }
    } catch (error) {
      console.error('Error al cargar datos:', error);
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando dashboard...</p>
        </div>
      </div>
    );
  }

  const statCards = [
    {
      name: 'Total Reservas',
      value: stats?.reservations?.total || 0,
      icon: Calendar,
      color: 'bg-blue-500',
      href: '/admin/reservations',
    },
    {
      name: 'Pendientes',
      value: stats?.reservations?.pending || 0,
      icon: Clock,
      color: 'bg-yellow-500',
      href: '/admin/reservations?status=pending',
    },
    {
      name: 'Mensajes sin Leer',
      value: stats?.messages?.unread || 0,
      icon: MessageSquare,
      color: 'bg-red-500',
      href: '/admin/messages?status=unread',
    },
    {
      name: 'Álbumes',
      value: stats?.gallery?.albums || 0,
      icon: Image,
      color: 'bg-purple-500',
      href: '/admin/gallery',
    },
  ];

  const quickLinks = [
    { name: 'Equipo Pastoral', count: stats?.team || 0, icon: Users, href: '/admin/team', color: 'text-blue-600' },
    { name: 'Grupos Parroquiales', count: stats?.groups || 0, icon: UsersRound, href: '/admin/parish-groups', color: 'text-green-600' },
    { name: 'Banners', count: stats?.banners || 0, icon: FileText, href: '/admin/banners', color: 'text-orange-600' },
    { name: 'Imágenes', count: stats?.gallery?.images || 0, icon: Image, href: '/admin/gallery', color: 'text-purple-600' },
  ];

  return (
    <div>
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
        <p className="text-gray-600 mt-1">
          Bienvenido al panel de administración de la Parroquia
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {statCards.map((stat) => {
          const Icon = stat.icon;
          return (
            <Link
              key={stat.name}
              href={stat.href}
              className="bg-white rounded-lg shadow-sm p-6 hover:shadow-md transition-shadow"
            >
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">{stat.name}</p>
                  <p className="text-2xl font-bold text-gray-900 mt-2">{stat.value}</p>
                </div>
                <div className={`${stat.color} p-3 rounded-lg`}>
                  <Icon className="w-6 h-6 text-white" />
                </div>
              </div>
            </Link>
          );
        })}
      </div>

      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
        {quickLinks.map((link) => {
          const Icon = link.icon;
          return (
            <Link
              key={link.name}
              href={link.href}
              className="bg-white rounded-lg shadow-sm p-4 hover:shadow-md transition-shadow flex items-center gap-3"
            >
              <Icon className={`w-8 h-8 ${link.color}`} />
              <div>
                <p className="text-2xl font-bold text-gray-900">{link.count}</p>
                <p className="text-sm text-gray-600">{link.name}</p>
              </div>
            </Link>
          );
        })}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        <div className="bg-white rounded-lg shadow-sm">
          <div className="p-6 border-b border-gray-200">
            <div className="flex items-center justify-between">
              <h2 className="text-lg font-semibold text-gray-900">Reservaciones Recientes</h2>
              <Link href="/admin/reservations" className="text-sm text-blue-600 hover:text-blue-800">
                Ver todas →
              </Link>
            </div>
          </div>
          <div className="divide-y divide-gray-200">
            {recentReservations.length > 0 ? (
              recentReservations.map((reservation) => (
                <div key={reservation.id} className="p-4 hover:bg-gray-50">
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="font-medium text-gray-900">
                        {reservation.nombre} {reservation.apellidos}
                      </p>
                      <p className="text-sm text-gray-500">
                        {reservation.tipo_misa} • {reservation.reservation_date}
                      </p>
                    </div>
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${STATUS_COLORS[reservation.status] || 'bg-gray-100 text-gray-800'}`}>
                      {STATUS_LABELS[reservation.status] || reservation.status}
                    </span>
                  </div>
                </div>
              ))
            ) : (
              <div className="p-8 text-center text-gray-500">
                <Calendar className="w-12 h-12 mx-auto mb-2 text-gray-400" />
                <p>No hay reservaciones recientes</p>
              </div>
            )}
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm">
          <div className="p-6 border-b border-gray-200">
            <div className="flex items-center justify-between">
              <h2 className="text-lg font-semibold text-gray-900">Mensajes Recientes</h2>
              <Link href="/admin/messages" className="text-sm text-blue-600 hover:text-blue-800">
                Ver todos →
              </Link>
            </div>
          </div>
          <div className="divide-y divide-gray-200">
            {recentMessages.length > 0 ? (
              recentMessages.map((message) => (
                <div key={message.id} className={`p-4 hover:bg-gray-50 ${!message.is_read ? 'bg-blue-50' : ''}`}>
                  <div className="flex items-start justify-between">
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center gap-2">
                        <p className="font-medium text-gray-900 truncate">{message.nombre}</p>
                        {!message.is_read && <span className="w-2 h-2 bg-blue-600 rounded-full"></span>}
                      </div>
                      <p className="text-sm text-gray-600 truncate">{message.asunto}</p>
                      <p className="text-xs text-gray-400 mt-1">
                        {new Date(message.created_at).toLocaleDateString('es-PE')}
                      </p>
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <div className="p-8 text-center text-gray-500">
                <MessageSquare className="w-12 h-12 mx-auto mb-2 text-gray-400" />
                <p>No hay mensajes recientes</p>
              </div>
            )}
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="bg-yellow-50 border-l-4 border-yellow-400 p-4 rounded">
          <div className="flex">
            <AlertCircle className="w-5 h-5 text-yellow-400" />
            <div className="ml-3">
              <h3 className="text-sm font-medium text-yellow-800">Reservas pendientes</h3>
              <p className="text-sm text-yellow-700 mt-1">
                Tienes {stats?.reservations.pending || 0} reservas esperando confirmación.
              </p>
              <Link href="/admin/reservations?status=pending" className="text-sm font-medium text-yellow-800 hover:text-yellow-900 mt-2 inline-block">
                Revisar ahora →
              </Link>
            </div>
          </div>
        </div>

        <div className="bg-green-50 border-l-4 border-green-400 p-4 rounded">
          <div className="flex">
            <CheckCircle className="w-5 h-5 text-green-400" />
            <div className="ml-3">
              <h3 className="text-sm font-medium text-green-800">Sistema funcionando</h3>
              <p className="text-sm text-green-700 mt-1">
                Todas las APIs están operativas y la base de datos está conectada.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
